*{
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
 
}

body{
  overflow-x: hidden;
  scroll-behavior: smooth;
  font-family: sans-serif;
}

li{
  list-style: none;
}

a{
  text-decoration: none;
  font-family:  "Montserrat";
}

h1{
  font-family:  "Montserrat";
  font-weight: 200;
}

h2{
  font-family:  "Montserrat";
  font-weight: 200;
}

h3{
  font-family:  "Montserrat";
  font-weight: 200;
}

h4{
  font-family:  "Montserrat";
  font-weight: 200;
}

.container{
  width: 90%;
  margin: 0 auto;
}

.reveal{
  position: relative;
  transform: translateY(150px);
  opacity: 0;
  transition: all 1s ease;
}

.reveal.active{
  transform: translateY(0px);
  opacity: 1;
}

.revealtwo{
  position: relative;
  transform: translateX(150px);
  opacity: 0;
  transition: all 1s ease;
}

.revealtwo.activetwo{
  transform: translateX(0px);
  opacity: 1;
}

.container_flex{
  display: flex;
  justify-content: space-between;
  width: 90%;
  margin: 0 auto;
}

.btn{
  padding: .8rem .5rem;
  border: none;
  border-radius: 5px;
  box-shadow: 1px 1px 5px #00000054;
  background-color: #355e3b;
  color: #fff;
  text-decoration: none;
  border-bottom: 2px solid #ffffff00;
  font-weight: 300;
  transition: all 500ms ease;
}

.btn:hover{
  border-bottom: 2px solid #ffffff;
  transform: translateY(-5px);
  box-shadow: 1px 1px 5px #000000a4;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}

a{
  text-decoration: none;
}

@media (max-width: 768px){
  html{
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
  }
  .container_flex{
    flex-direction: column;
    justify-content: center;
  }
}