header{
    width: 100vw;
    height: 100vh;
}

.header_background{
    width: 60%;
    height: 70vh;
    position: relative;
    left: 40%;
    bottom: 70%;
    background-color: #ffd9006c;
}

.header_h1{
    font-size: 6rem;
    position: relative;
    font-weight: 200;
    bottom: 73%;
    left: 10%;
    color: #355e3b9a;
}

.header_text{
    width: 46%;
    margin-top: 4rem;
    z-index: 1;
}

.header_text h1{
    font-size: 3.5rem;
    padding: 1.5rem 0;
}

.header_text p{
    font-weight: 200;
}

.header_text .btn_div{
    padding-top: 4rem;
}

.header_img{
    width: 46%;
    z-index: 1;
    margin-top: 2rem;
}

header .container_flex{
    padding-top: 6rem;
}

.header_img-cont1{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fff;
    z-index: 1;
    width: 45%;
    animation: bounce 2s infinite;
    background-color: #355e3b;
    border: 5px solid #ffff;
    box-shadow: 2px 2px 10px #00000023;
    padding: .8rem;
    position: relative;
    top: 6%;
}

.header_img-cont1 p{
    font-size: .8rem;
    font-weight: 100;
}

.stars ul{
    display: flex;
    z-index: 10;
    width: 30%;
}

.stars ul li{
    list-style: none;
}

.stars ul li img{
    width: 60%;
}

.header_img-cont2{
    background-image: url(../images/img22.jpeg);
    background-position: center;
    background-size: cover;
    background-repeat: none;
    position: relative;
    left: 2rem;
    width: 33vw;
    height: 43vh;
}

.header_img-cont3{
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 1;
    width: 37%;
    height: 25%;
    position: relative;
    bottom: 7%;
    left: 40%;
    background-color: #fff;
    border: 8px solid #ffd9006c;
    padding: .8rem;
}

.header_img-cont3 p{
    font-size: .8rem;
    font-weight: 100;
}

.cont3_content{
    display: flex;
    justify-content: space-between;
    gap: 1rem;
    align-items: center;

}

.cont3_content img{
    width: 7%;
    animation: bounce 2s infinite;
}
   
@media (max-width: 768px){
    .header_text{
        width: 90%;
        margin: 4rem auto;
        z-index: 1;
        text-align: center;
    }   

    .header_img{
        width: 90%;
    }

    .header_img-cont1{
        width: 60%;
    }

    .header_img-cont2{
        width: 90%;
    }

    .header_img-cont3{
        width: 60%;
    }

    .header_h1{
        font-size: 6rem;
        position: relative;
        font-weight: 200;
        bottom: 75%;
        z-index: 10;
        left: 12%;
        color: #ffd90059;
    }

    
.header_background{
    width: 80%;
    height: 60vh;
    position: relative;
    left: 20%;
    bottom: 65%;
    background-color: #ffd9003e;
}
}

@media (max-width: 480px){

   header{
    margin-bottom: 5rem;
   }

    .header_text h1{
        font-size: 2rem;
    }

    .header_img-cont2{
        background-image: url(../images/img22.jpeg);
        background-position: center;
        background-size: cover;
        background-repeat: none;
        position: relative;
        left: 2rem;
        width: 80vw;
        height: 30vh;
    }
}