.offers_section{
    padding: 4rem 0;
}

.offers_section .container_flex{
    align-items: center;
}

.offers_section h1{
    margin-left: 3.6rem;
    font-size: 2rem;
    padding: 2rem 0;
    text-align: center;
}

.offer_top{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 2px solid goldenrod;
    padding: 1.5rem 0;
}

.off1{
    width: 16%;
   text-align: center;
}

.off2{
    width: 45%;
    text-align: center;
}

.off3{
    width: 43%;
    text-align: center;
}

.container_offer{
    display: grid;
    width: 45%;
    gap: .5rem;
    justify-content: center;
    grid-template-columns: repeat(1, 90%);
}

.offer_content{
    padding: .5rem;
}

.offer_contact{
    display: flex;
    padding: .8rem 0;
    justify-content: space-between;
}

.offer_contact div{
    gap: 1rem;
    align-items: center;
}

.offer_contact a{
    font-size: .9rem;
    color: grey;
    text-decoration: none;
    transition: all 500ms ease;
    border-bottom: 1px solid #ffd90000;
}

.offer_contact a:hover{
    border-bottom: 1px solid #ffd900a4;
    transform: translateY(-5px);
}

.offer_contact img{
    width: 5%;
    padding-right: 1rem;
    align-items: center;
    animation: bounce 2s infinite;
}

.offer_images img{
    max-width: 100%;
    height: auto;
    vertical-align: middle;
    display: inline-block;
}

.offer_images > div{
    display: flex;
    justify-content: center;
    align-items: center;
}

.offer_images > div > img{
    width: 100%;
    height: 100%;
    transition: all 500ms ease;
    object-fit: cover;
    border-radius: 5px;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0);
}

.offer_images > div > img:hover{
    box-shadow: 1px 1px 5px #000000cf;
    transform: translateY(-7px); 
}

.offer_images{
    display: grid;
    width: 60%;
    grid-gap: 10px;
    grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
    grid-auto-rows: 200px;
    grid-auto-flow: dense;
}

.offer_images .wide{
   grid-column: span 2;
}

.offer_images .tall{
    grid-column: span 1;
    grid-row: span 2;
 }

 .offer_images .big{
    grid-column: span 2;
    grid-row: span 2;
 }

 @media (max-width: 768px){
    .container_offer{
        display: grid;
        width: 90%;
        gap: .5rem;
        justify-content: center;
        grid-template-columns: repeat(1, 100%);
    }

    .offer_images{
        width: 100%;
    }

    .offers_section h1{
        margin: 0 auto;
    }
    
 }

 @media (max-width: 480px){
    .container_offer{
        width: 100%;
        padding: 3rem;
    }
    .offers_section h1{
        font-size: 1.4rem;
        width: 80%;
    }

    .off2{
        width: 30%;
        text-align: center;
        font-size: .7rem;
    }

    .offer_contact img{
       width: 7%;
    }

    .offer_contact div{
        gap: 0;
    }
    
    .offer_contact a{
      font-size: .7rem;
    }
 }