.packages{
    padding: 3rem 0;
}

.packages .container_flex{
    align-items: center;
}

.packages_section{
    width: 55%;
    display: grid;
    grid-template-columns: repeat(2, 50%);
    gap: 1rem;
}

.package{
    box-shadow: 1px 1px 5px #00000034;
    padding: 1rem;
    border-radius: 5px;
}

.package_name{
    display: flex;
    gap: 1rem;
    align-items: center;
}

.package_name h1{
    padding: .3rem 1rem;
    border-radius: 5px;
    color: #fff;
    background-color: goldenrod;
}

.package_list{
    padding: .5rem 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.package_list ul li{
   font-weight: 100;
   font-size: .8rem;
   padding-top: 5px;
   font-style: italic;
}


.package_price{
    display: flex;
    padding: .5rem 0;
    justify-content: space-between;
    align-items: center;
}

.package_img{
    width: 40%;
    display: grid;
    grid-template-rows: repeat(1, 1fr);
}

.package_owner{
    width: 70%;
    margin: 0 auto;
    border-radius: 5px;
    box-shadow: 2px 2px 5px #00000065;
    background-color: #335e3b;
}

.package_image-info{
    width: 100%;
    display: grid;
    align-items: center;
    justify-content: space-between;
    grid-template-columns: repeat(3, 30%);
}    

.package_info-1 h2{
    font-size: 1.2rem;
    padding: 1rem 0;
}

.package_1-loca{
    display: flex;
    gap: .2rem;
    align-items: center;
}

.package_1-loca img{
    width: 10%;
}

.package_info-1 p{
    font-weight: 100;
    font-size: .9rem;
}

.package_info-3 h2{
    font-size: 1.2rem;
}

.package_2-socials{
    font-weight: 100;
    font-size: .9rem;
    padding-top: 1rem;
    gap: .5rem;
    display: flex;
}

.package_2-socials a img{
   width: 30%;
}

.package_3-socials{
    font-weight: 100;
    font-size: .9rem;
    padding-top: 1rem;
    gap: .5rem;
    display: flex;
}

.package_3-socials a img{
   width: 60%;
}

@media (max-width: 768px){
    .packages_section{
        width: 100%;
        display: grid;
        grid-template-columns: repeat(2, 50%);
        gap: 1rem;
    }

    .package_img{
        width: 100%;
        display: grid;
        margin-top: 2.5rem;
        grid-template-rows: repeat(1, 1fr);
    }

    .package_owner{
        display: none;
    }
}

@media (max-width: 480px){
    .packages{
        padding: 5rem 0;
    }

    .package{
        padding: 0;
    }

    .packages_section{
        width: 100%;
        display: grid;
        grid-template-columns: repeat(1, 90%);
        gap: 1rem;
        justify-content: center;
    }
    .package_image-info{
        width: 100%;
    }
    .package_image-info h2{
        font-size: 1rem;
    }
}