.Gallery_link-section{
    padding: 4rem 0;
    height: 70vh;
    background-color: #1f1f1f;
    color: #fff;
}

.Gallery_link-section .container_flex{
    align-items: center;
    gap: 2rem;
}

.images img{
    max-width: 100%;
    height: auto;
    vertical-align: middle;
    display: inline-block;
    box-shadow: 1px 1px 5px #00000083;
}

.images > div{
    display: flex;
    justify-content: center;
    align-items: center;
}

.images > div > img{
    width: 100%;
    height: 100%;
    transition: all 500ms ease;
    object-fit: cover;
    border-radius: 5px;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0);
}

.images > div > img:hover{
    box-shadow: 1px 1px 5px #000000cf;
    transform: translateY(-7px); 
}

.images{
    display: grid;
    grid-gap: 10px;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    grid-auto-rows: 200px;
    grid-auto-flow: dense;
}

.images .wide{
   grid-column: span 2;
}

.images .tall{
    grid-column: span 1;
    grid-row: span 2;
 }

 .images .big{
    grid-column: span 2;
    grid-row: span 2;
 }

 .text{
    width: 45%;
 }

 .text_btn{
    padding-top: 2rem;
 } 

 .text h2{
    font-weight: 100;
    padding: 1.5rem 0;
    font-size: 2.5rem;
 }

.gallery_h1{
    font-size: 7rem;
    position: relative;
    font-weight: 200;
    bottom: 20%;
    z-index: 1;
    left: 30%;
    color: #355e3bb6;
}

@media (max-width: 768px){
    .Gallery_link-section{
        height: 60vh;
    }

    .images{
        width: 100%;
    }

    .text{
        width: 100%;
    }
}

@media (max-width: 480px){

    .Gallery_link-section{
        height: 120vh;
        padding: 4rem 0;
    }

   .images{
    margin-top: 3rem;
   }

    .text{
        text-align: center;
        z-index: 10;
        margin-top: 4rem;
    }

    .text h2{
       font-size: 1.7rem;
    }
    
    .gallery_h1{
        font-size: 6rem;
        position: relative;
        font-weight: 200;
        bottom: 1%;
        z-index: 1;
        left: 20%;
        color: #355e3bb6;
    }
}