.footer_section{
    background-color: #121212;
    padding-top: 6rem;
    color: #fff;
    font-weight: 100;
}

.container_flex-footer{
    display: flex;
    justify-content: space-between;
    width: 90%;
    margin: 0 auto;
}

.footer_logo{
    width: 5%;
    padding: .5rem 3.5rem;
}

.footer-1{
    width: 25%;
}

.footer_contact div{
    display: flex;  
    padding-top: 1rem;
    gap: 1rem;
    align-items: center;
}

.footer-1 h3{
    font-weight: 100;
}

.footer_contact div img{
    width: 5%;
}

.footer_contact div a{
    color: #aeaeae;
    text-decoration: none;
}

.footer-2{
    width: 45%;
    text-align: center;
}

.footer-2 h3{
    font-weight: 100;
    padding: .5rem 0;
}

.footer-2 p{
    color: #aeaeae;
    font-weight: 100;
}

.footer_socials{
    padding-top: 1rem;
}

.footer_socials ul{
    display: flex;
    justify-content: center;
    width: 50%;
    gap: 1rem;
    margin: 0 auto;
    padding-top: .5rem;
}
.footer_socials ul li{
    padding: .7rem;
    background-color: #333333;
    border-radius: 5%;
}

.footer_socials ul li a img{
    width: 40%;
}

.footer-3{
   width: 25%;
}

.footer-3 h3{
    font-weight: 100;
}

.footer-3 p{
    color: #aeaeae;
    font-weight: 100;
}

.footer_email{
    display: flex;
    padding: 1rem 0;
    gap: .5rem;
}

.footer_email input{
    border: none;
    background-color: transparent;
    border-bottom: 2px solid #fff;
}

.footer_email input::placeholder{
    color: #aeaeae;
}

.footer_hr-div{
    padding: 1.5rem 0;
}

.footer_hr{
    width: 90%;
    margin: 0 auto;
    padding: 1px 0;
    border: none;
    background-color: #fff;

}

.copyright{
    width: 80%;
    margin: 0 auto;
    padding: .5rem 0;
}

.copyright p{
    text-align: center;
    color: #aeaeae;
    font-weight: 100;
}

.copyright p a{
    color: #355e3b;
    font-weight: 500;
    border-bottom: 1px solid #daa52000;
    transition: all 300ms ease;
}

.copyright p a:hover{
    color: #355e3b;
    font-weight: 500;
    border-bottom: 1px solid #daa520;
}

@media (max-width: 768px){
   .container_flex-footer{
    flex: none;
    display: grid;
    grid-template-columns: repeat(2, 45%);
    gap: 1rem;
   }

   
.footer_logo{
    width: 10%;
    padding: .5rem 3.5rem;
}

   .footer-1{
    width: 100%;
   }

   .footer-2{
    width: 100%;
   }

   .footer_socials ul{
    width: 100%;
   } 

.footer_socials ul li a img{
    width: 35%;
}

   .footer-3{
    width: 100%;
 }
}

@media (max-width: 480px){
    .container_flex-footer{
        flex: none;
        display: grid;
        justify-content: center;
        grid-template-columns: repeat(1, 90%);
        gap: 4rem;
       }

       .copyright p{
        font-size: .7rem;
    }
 
}