.about_section{
    padding-top: 5rem;
    height: 100vh;
}

.about_section button{
    width: 50%;
}

.about_section h1{
    padding: .5rem 0;
}

.about_content-text{
    width: 47%;
    z-index: 1;
}

.service_list{
    display: flex;
    gap: 2rem;
}  

.service_list ul li{
    padding-top: .5rem;
    list-style: none;
    font-weight: 100;
    color: #355e3bde;
    font-style: italic;
    font-size: .9rem;
}

.about_contact{
    margin-top: 2rem;
}

form{
    width: 60%;
}

.form-group{
    gap: 2rem;
    padding: .7rem 0;
    border-bottom: 2px sold #383838;
}

input[type="text"],
input[type="tel"],
input[type="email"],
textarea {
  width: 100%;
  padding: .5rem;
  font-size: 1rem;
  border: none;
  border: 10px;
  border-bottom: 2px solid #355e3b;
  outline: none;
  box-shadow: 1px 1px 2px #0000004d;
  background-color: #ffffff;
  transition: background-color 0.3s ease;
}  

input::placeholder{
    color: grey;
    font-size: .9rem;
    font-family: sans-serif;
}

textarea::placeholder{
    color: grey;
    font-size: .9rem;
    font-family: sans-serif;
}

input[type="text"]:focus,
input[type="tel"]:focus,
input[type="email"]:focus,
textarea:focus {
  background-color: #ffffffe3;
  outline: none;
}
  
textarea {
    resize: vertical;
} 

.btns{
    width: 70%;
    background-color: aquamarine;
    text-decoration: none;
    padding: 1rem;
    border: none;
}

.about_content-img{
    width: 50%;
    z-index: 1;
}

.about_img2{
    border-radius: 63% 37% 44% 56% / 61% 51% 49% 39%; 
    background-color: #355e3b;
    position: absolute;
    box-shadow: 2px 2px 10px #0000007a;
    width: 30vw;
    z-index: -1;
    right: 10%;
    display: none;
    height: 60vh;
}

.about_img1{
    background-image: url(../images/owner.png);
    background-position: center;
    background-size: cover;
    background-repeat: none;
    position: relative;
    z-index: 1;
    left: 20%;
    width: 35vw;
    background-color: #355e3b;
    border-radius: 5px;
    box-shadow: 2px 2px 5px #00000085;
    height: 65vh;
}

.about_img-content{
    background-color: #fff;
    padding: 1.5rem;
    width: 45%;
    border-bottom: 2px solid #355e3b;
    border-radius: 5px;
    height: 25%;
    animation: bounce 2s infinite;
    box-shadow: 1px 1px 5px #00000042;
    z-index: 1;
    position: relative;
    bottom: 10%;
}

.about_img-content p{
    color: #383838;
    font-style: italic;
}

.about_content-img h2{
    padding: 1rem 0;
}


.about_background{
    width: 60%;
    height: 50vh;
    position: relative;
    left: 40%;
    bottom: 81%;
    background-color: #ffd9006c;
}

.about_h1{
    font-size: 9rem;
    position: relative;
    font-weight: 200;
    bottom: 85%;
    left: 35%;
    color: #355e3b9a;
}

@media (max-width: 768px){
    .about_section{
        height: 130vh;
        margin-top: 8rem;
    }

    .about_section .container_flex{
        gap: 2rem;
    }

    .about_content-text{
        width: 100%;
        display: grid;
        margin: 0 auto;
        grid-template-columns: repeat(2, 50%);
        justify-content: space-between;
        z-index: 1;
        align-items: center;
    }

    .about_contact {
        text-align: center;
        padding: 0;
        
    }

    .form_grouped{
        width: 90%;
        margin: 0 auto;
    }

    .form-group{
        margin-right: 1rem;
    }

    .about_content-img{
        width: 90%;
        margin: 0 auto;
         z-index: 1;
    }

    .about_img2{
      width: 80%;
    }
    
    .about_img1{
        left: 0;
        margin: 0 auto;
        border-radius: 10px;
        width: 70vw;
   }

   .about_background{
    width: 80%;
    height: 70vh;
    position: relative;
    left: 20%;
    bottom: 67.5%;
    background-color: #ffd9003e;
}

.about_h1{
    font-size: 10rem;
    position: relative;
    font-weight: 200;
    bottom: 90%;
    left: 35%;
    color: #ffd90059;
}
}


@media (max-width: 480px){
    .about_section{
        height: 150vh;
        margin-top: 10rem;
    }


    .about_content-text{
        width: 100%;
        display: grid;
        margin: 0 auto;
        grid-template-columns: repeat(1, 100%);
        justify-content: center;
        z-index: 1;
        align-items: center;
    }

    .about_content-img{
        width: 100%;
        margin: 0 auto;
        z-index: 1;
    }

    .about_img2{
        width: 90%;
        margin: 0 auto;
        background-color: #355e3b;
        display: none;
    }

    .about_img1{
        left: 0;
        margin: 0 auto;
        border-radius: 10px;
        width: 90vw;
        height: 50vh;
        background-color: #355e3b;
   }

   .about_img-content{
        width: 90%;
   }
}