.about_company{
    padding-top: 5rem;
    height: 60vh;
    color: #fff;
}

.company_images img{
    max-width: 100%;
    height: auto;
    vertical-align: middle;
    display: inline-block;
    box-shadow: 1px 1px 5px #00000083;
}

.company_images  > div{
    display: flex;
    justify-content: center;
    align-items: center;
}

.company_images  > div > img{
    width: 100%;
    height: 100%;
    transition: all 500ms ease;
    object-fit: cover;
    border-radius: 5px;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0);
}

.company_images  > div > img:hover{
    box-shadow: 1px 1px 5px #000000cf;
    transform: translateY(-7px); 
}

.company_images {
    display: grid;
    width: 40%;
    z-index: 1;
    grid-gap: 10px;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    grid-auto-rows: 200px;
    grid-auto-flow: dense;
}

.company_images  .wide{
   grid-column: span 2;
}

.company_images  .tall{
    grid-column: span 1;
    grid-row: span 2;
 }

 .company_images .big{
    grid-column: span 2;
    grid-row: span 2;
 }


.company_text{
    width: 45%;
    z-index: 1;
    margin-top: 3.5rem;
}

.company_text h2{
    padding: .6rem 0;
    font-size: 2.5rem;
    font-weight: 100;
}

.company_text p{
    padding: .5rem 0;
}

.company_text ul li{
    padding: .5rem 0;
    font-weight: 100;
    font-style: italic;
}

.company_text .btns{
    margin-bottom: .5rem;
}

.about_btn{
    padding-top: 1.5rem;
}


.aboutcompany_background{
    width: 70%;
    height: 65vh;
    position: relative;
    left: 40%;
    bottom: 100%;
    background-color: /*#355e3b*/#1f1f1f;
}

@media (max-width: 768px){
    .about_company{
        margin-top: 1rem;
        height: 70vh;
    }

    .about_company .container_flex{
        gap: 3rem;
    }

    .company_images{
        width: 80%;
        margin: 0 auto;
        z-index: 1;
    }

    .company_text{
        width: 90%;
        z-index: 1;
    }

    
.company_img-1{
    background-image: url(../images/img27.jpeg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    height: 40vh;
}

.company_img-2{
    width: 55%;
    height: 55%;
    position: relative;
    bottom: 0;
    padding-bottom: 10%;
    padding-left: 10%;
}
 
.aboutcompany_background{
    width: 100%;
    height: 60vh;
    position: relative;
    left: 5%;
    bottom: 70%;
}

}

@media (max-width: 480px){
    .company_text{
        width: 100%;
        z-index: 1;
        text-align: center;
     }

    .company_images{
        width: 100%;
        margin: 0 auto;
        z-index: 1;
    }
    .aboutcompany_background{
        width: 100%;
        height: 70vh;
        position: relative;
        left: 5%;
        bottom: 70%;
    }
}