.gallery{
    padding: 4rem 0;
}

.gallery h1{
    text-align: center;
    font-size: 3rem;
    padding: 2rem 0;
} 

.grid-wrapper img{
    max-width: 100%;
    height: auto;
    vertical-align: middle;
    display: inline-block;
}

.grid-wrapper > div{
    display: flex;
    justify-content: center;
    align-items: center;
}

.grid-wrapper > div > img{
    width: 100%;
    height: 100%;
    transition: all 500ms ease;
    object-fit: cover;
    border-radius: 5px;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0);
}

.grid-wrapper > div > img:hover{
    box-shadow: 1px 1px 5px #000000cf;
    transform: translateY(-7px); 
}

.grid-wrapper{
    display: grid;
    grid-gap: 10px;
    grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
    grid-auto-rows: 200px;
    grid-auto-flow: dense;
}

.grid-wrapper .wide{
   grid-column: span 2;
}

.grid-wrapper .tall{
    grid-column: span 1;
    grid-row: span 2;
 }

 .grid-wrapper .big{
    grid-column: span 2;
    grid-row: span 2;
 }

 .btn_gallery{
    padding: .8rem 1rem;
    border: 2px solid teal;
    background-color: #0080807e;
    text-decoration: none;
    color: #fff;
}

.gallery_btn{
    margin: 1rem auto;
    display: flex;
    justify-content: center;
}