.review_section{
    padding: 6rem 0;
    height: 95vh;
}

.review_section h1{
    text-align: center;
    padding: 2rem 0;
    font-size: 2.5rem;
} 

video{
    border-radius: 10px;
    z-index: 10;
}

.review_section .container_flex{
    align-items: center;
}

.review_container{
    width: 70%;
    z-index: 10;
}

.review_text{
    width: 70%;
    z-index: 10;
}

.review_text h1{
    font-size: 2.5rem;
    position: relative;
    font-weight: 100;
    left: 17%;
}

.review{
    width: 70%;
    box-shadow: 1px 1px 4px #000000a4;
    padding: 2rem;
    background-color: white;
}

.review_stat{
  z-index: 1; 
  position: relative;
  left: 17%;
  top: 28%;
}

.review_stats{
    display: flex;
    gap: 2rem;
    width: 80%;
    background-color: #355e3b;
    padding: 1rem;
}

.review_stats h3{
    color: #fff;
}

.review_stats ul{
    display: flex;
    width: 30%;
}

.review_stats ul li{
    list-style: none;
}

.review_stat ul li img{
    width: 60%;
}

.review_video{
    width: 50%;
}

.review_video img{
    width: 95%;
    position: relative;
    right: 8%;
}

.review_h1{
    font-size: 20rem;
    position: relative;
    font-weight: 200;
    bottom: 10%;
    left: 10%;
    color: #355e3ba8;
}

.review_background{
    width: 90%;
    height: 60vh;
    position: relative;
    right: 30%;
    bottom: 95%;
    background-color: #1f1f1f;
}

@media (max-width: 768px){
    .review_section{
        margin-top: 3rem;
    }

    .review_section .container_flex{
      width: 90%;
    }

    .review_text{
        width: 70%;
    }

    .review_container{
        display: flex;
        width: 100%;
    }

    .review_stat{
        z-index: 1; 
        position: relative;
        top: 2%;
        left: 3%;
        }
      
      .review_stats{
          display: flex;
          gap: 2rem;
          width: 80%;
          background-color: #355e3b;
          padding: 1rem;
      }
      

    .review_text h1{
        padding: 2rem 0;
    }

    .review_video{
        width: 80%;
        margin-left: 4rem;
    }

    .review_background{
        width: 90%;
        height: 40vh;
        position: relative;
        right: 10%;
        bottom: 60%;
        background-color: #1f1f1f;
    }
    
}

@media (max-width: 480px){
    .review_section{
        margin-top: 20rem;
        height: 100vh;
    }

    video{
        margin-top: 2rem;
    }

    .review_section .container_flex{
      width: 90%;
    }

    .review_container{
        display: none;
    }

    .review_text{
        width: 90%;
    }

    .review{
        width: 86.5%;
        font-size: 1rem;
    } 

    .review_stat{
        z-index: 1; 
        position: relative;
        left: 10%;
        top: 35%;
        width: 95%;
      }

    .review_text h1{
        padding: 2rem 0;
        text-align: center;
        left: 0;
        font-size: 2rem;
    }

    .review_video{
        width: 100%;
        margin-left: 2rem;
    }

    .review_section h1{
        margin-top: 6rem;
    } 

    .review_h1{
        font-size: 8rem;
        position: relative;
        font-weight: 200;
        bottom: 2%;
        z-index: 2;
        left: 25%;
        color: #ffd90059;
    }


}