.service_section{
    text-align: center;
    font-weight: 100;
    padding: 3rem 0;
    color: #fff;
    background-color: #1f1f1f;
}

.service_section h1{
    font-weight: 100;
}

.services{
    display: flex;
    justify-content: space-between;
    padding: .5rem 0;
}

.service{
    text-align: center;
    display: flex;
    gap: 2rem;
    align-items: center;
    padding: 3rem 0;
    justify-content: center;
    width: 10%;
}

.service_img{
    width: 20%;
    height: 20%;
    padding: 1rem;
    box-shadow: 1px 1px 5px #00000083;
    /*background-color: #fff;*/
    border-radius: 5%;
    border: 1px solid #d9a52125;
    margin: 1rem auto;
    transition: all 500ms ease;
}

.service_img:hover{
    border: 1px solid #daa520;
    transform: translateY(-5px);
}

.service_img img{
    width: 100%;
    margin: 0 auto;
}

.service p{
    width: 70%;
    margin: 0 auto;
    text-align: center;
    color: goldenrod;
    font-weight: 100;
}

.slick-slide > div{
    margin: 0 10px;
}

@media (max-width: 480px){
    .service_section{
        margin: 4rem 0;
    }
}