.newsletter{
    background-color: #335e3b;
    margin-top: 3rem;
}

.container_flex-news{
    display: flex;
    justify-content: space-between;
    width: 90%;
    margin: 0 auto;
}

.sign_img-1{
    width: 20%;
    z-index: 1;
}

.sign_img-1 img{
    width: 100%;
    border: 4px solid #335e3b;
    z-index: 1;
    position: relative;
    top: 30%;
}

.sign_content{
    width: 40%;
    text-align: center;
    margin-top: 3.5rem;
}

.sign_content h2{
    padding: 1rem 0;
    font-weight: 100;
    color: #fff;
}

.sign_input{
    display: flex;
}

.sign_img-2{
    width: 20%;
}  

.sign_img-2 img{
    width: 100%;
    border: 4px solid #335e3b;
    z-index: 1;
    position: relative;
    bottom: 30%;
}

@media (max-width: 768px){
    .newsletter{
        padding: 1rem 0;
    }
  

    .sign_img-2 img{
        width: 100%;
        border: 4px solid #335e3b;
        z-index: 1;
        position: relative;
        bottom: 50%;
    }

    .sign_content{
        width: 50%;
        text-align: center;
        margin: 0 auto;
    }

    .sign_img-1 img{
        width: 100%;
        border: 4px solid #335e3b;
        z-index: 1;
        position: relative;
        top: 70%;
    }
}

@media (max-width: 480px){
    .sign_img-2 img{
       display: none;
    }

    .sign_content{
        width: 90%;
        text-align: center;
        margin: 0 auto;
    }

    .sign_content h2{
        font-size: 1.2rem;
    }

    .sign_img-1 img{
        display: none;
    }
}