.faq_section{
    padding: 3rem 0;
}

.faq_section h1{
    font-size: 2.5rem;
    padding: 2rem 0;
    margin-left: 4rem;
}

.container_flex-faq{
    display: flex;
    justify-content: space-between;
    width: 90%;
    margin: 0 auto;
}

.faq_images{
    width: 40%;
    display: grid;
    grid-gap: 10px;
    grid-template-columns: repeat(auto-fit, minmax(110px, 1fr));
    grid-auto-rows: 200px;
    grid-auto-flow: dense;
}


.faq_images img{
    max-width: 100%;
    height: auto;
    vertical-align: middle;
    display: inline-block;
}

.faq_images > div{
    display: flex;
    justify-content: center;
    align-items: center;
}

.faq_images > div > img{
    width: 100%;
    height: 100%;
    transition: all 500ms ease;
    object-fit: cover;
    border-radius: 5px;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0);
}

.faq_images > div > img:hover{
    box-shadow: 1px 1px 5px #000000cf;
    transform: translateY(-7px); 
}

.faq_images .wide{
   grid-column: span 2;
}

.faq_images .tall{
    grid-column: span 1;
    grid-row: span 2;
 }

 .faq_images .big{
    grid-column: span 2;
    grid-row: span 2;
 }

 .faq-list {
    border-radius: 8px;
    width: 50%;
    display: flex;
    justify-content: center;
    display: grid;
    grid-template-columns: repeat(1, 100%);
    gap: 2rem;
 } 

.faq-item:last-child {
    border-bottom: none;
}

.faq-question {
    padding: 1.5rem 2rem;
    cursor: pointer;
    border-radius: 5px;        
    background-color: #ffffff;
    margin: 0 auto;
    border-top: 2px solid goldenrod;
    align-items: center;
    box-shadow:  -3px -3px 10px #16161641, 3px 3px 10px #2c2c2c33;
    transition: background-color 0.3s ease;
}

.faq-question:hover {
    background-color: #f1f1f1;
}

.faq-answer{ 
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s ease, padding 0.3s ease;
     color: #555;
}

.faq-answer.show {
    max-height: 200px; /* A value large enough for the answer */
    padding: 1.5rem 1rem; /* Add padding when shown */
    background-color: #fafafa;
}

.arrow {
    margin-left: 10px;
    color: #335e3b;
    transition: transform 0.3s ease;
}

.arrow {
    transform: rotate(0deg);
}

.arrow.show {
    transform: rotate(180deg);
}

@media (max-width: 768px){
    .faq_section h1{
      margin-left: 2.5rem;
    }
    
    .container_flex-faq{
        align-items: center;
    }
    
    .faq_images{
        width: 45%;
  }
  .faq-list {
    width: 50%;
    gap: 2rem;
 } 
}

@media (max-width: 480px){
    .faq_section h1{
        margin: 0 auto;
        font-size: 1.7rem;
        width: 90%;
        text-align: center;
      }


    .container_flex-faq{
        flex-direction: column;
        width: 90%;
        gap: 2rem;
    }
    .faq_images{
        width: 100%;
   }
  .faq-list {
    width: 100%;
   } 
}